import React, { Component } from 'react'
import { withStyles } from '@material-ui/core/styles'
import styles from './index.styles'
import { TextField, Slider, ListItemSecondaryAction, List, ListItem, Divider, ListItemText, ListItemIcon, IconButton } from '@material-ui/core';
import { FontDownload, SpaceBar } from '@material-ui/icons'


class Modifiers extends Component {
    render = () => {
        
        const {classes} =  this.props
        //  console.log(this.props)
        //let spaceMarks = []
        //console.log(spaceMarks)
        return(
            <div className={classes.modifiers}>
                <TextField 
                    label="Meme Text"
                    rows={3}
                    multiline={true}
                    fullWidth={true}
                    value={this.props.memeCaption}
                    onChange={this.props.onTextChange}
                />
                <List>
                    <ListItem>
                        <ListItemIcon>
                            <SpaceBar/>
                        </ListItemIcon>
                        <ListItemText 
                            primary={
                                <Slider 
                                    value={this.props.padding}
                                    step={8}
                                    valueLabelDisplay="auto"
                                    min={0}
                                    max={80}
                                    marks={true}
                                    name="padding"
                                    onChange={(e, val) => this.props.onSliderChange(val, 'padding')}
                                />
                            }
                        />
                        <ListItemSecondaryAction>
                            <IconButton><span className={classes.btn}>{this.props.padding}</span></IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider varient="inset" component="li"/>
                    <ListItem>
                        <ListItemIcon>
                            <FontDownload/>
                        </ListItemIcon>
                        <ListItemText 
                            primary={
                                <Slider 
                                    value={this.props.font}
                                    step={4}
                                    valueLabelDisplay="auto"
                                    min={8}
                                    max={40}
                                    marks={true}
                                    name="font"
                                    onChange={(e, val) => this.props.onSliderChange(val, 'font')}
                                    
                                />
                            }
                        />
                        <ListItemSecondaryAction>
                            <IconButton><span className={classes.btn}>{this.props.font}</span></IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider varient="inset" component="li"/>
                </List>
            </div>
        )
    }
}


export default withStyles(styles)(Modifiers)