import React, { Component } from 'react'
import { withStyles } from '@material-ui/core/styles'
import styles from './index.styles'


class MemeSection extends Component {

    replaceHTML = (searchText, searchAndReplace) => {
        //let el = [];
        let text = ''
        let count = 0
        searchText.replace(/\r\n|\r|\n/g,"<br />")
        for(let i = 0; i < searchAndReplace.length; i++){
            //console.log(i, searchText.search(searchAndReplace[i][1]))
            while(searchText.search(searchAndReplace[i][1]) !== -1 && count < 8) {
                text = searchText.match(searchAndReplace[i][1])
                //console.log(text)
                try{
                //if(text){
                    //console.log("Entered")
                    searchText = searchText.replace(
                        searchAndReplace[i][1],
                        `<span class='${searchAndReplace[i][0]}'>${text[1]}</span>`)
                    //console.log(searchText)
                //}
                }catch(e) {
                    console.error("Error caused", i, text, searchText, e)
                }
                count += 1
            }
        }
        return searchText
    }

    writeCaption = (classes) => {
        //let content = []
        let searchAndReplace = [
            [classes.bold, new RegExp('\\*\\*([a-zA-z0-9\'\\s]*)\\*\\*')],
            [classes.italic, new RegExp('__([a-zA-z0-9\'\\s]*)__')],
            [classes.strike, new RegExp('~~([a-zA-z0-9\'\\s]*)~~')]
        ]
        //check for boldness
        //content = [
        const content = this.replaceHTML(this.props.memeCaption, searchAndReplace)
        //]
        return content

    }

    render = () => {
        //console.log(this.props.memeCaption)
        const {classes} =  this.props
        return(
            <div className={classes.memeHolder}>
                {/* <pre className={classes.textHolder} dangerouslySetInnerHTML={{__html: this.writeCaption(classes)}} style={{padding: this.props.padding, fontSize: this.props.font}}>

                </pre> */}
               <pre className={classes.textHolder}>
                   <h4 style={{lineHeight: 1.6, padding: this.props.padding, fontSize: this.props.font, fontWeight: 'lighter', opacity: '0.9'}}>{this.props.memeCaption}</h4>
               </pre>
               <h1 className={classes.background}>TCT</h1>
               <span className={classes.insta}>@thecheaptalkers</span>
               <span className={classes.page}>[tct]</span>
            </div>
        )
    }
}


export default withStyles(styles)(MemeSection)