const styles = theme => {
    return ({
        memeHolder: {
            height: '100vw',
            width: '100vw',
            background: '#212121',
            color:'#fff',
            position: 'relative',
            display: 'flex'
        },
        textHolder: {
            display: 'flex',
            width: '100%',
            alignItems: 'center',
            fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
            whiteSpace: 'pre-wrap'
        },
        insta: {
            position: 'absolute',
            top: theme.spacing(1),
            right: theme.spacing(1),
            opacity: 0.8
        },
        page: {
            position: 'absolute',
            bottom: theme.spacing(1),
            left: theme.spacing(1),
            textTransform: 'uppercase',
            opacity: 0.8
        },
        bold: {
            fontWeight: 'bold'
        },
        strike: {
            textDecoration: 'line-through'
        },
        italic: {
            fontStyle: 'italic'
        },
        background: {
            color: '#aaa',
            opacity: 0.1,
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            fontSize: 100,
            fontWeight: 'lighter',
            margin: 0
        }
    })
}


export default styles